import React from 'react';

import { SxProps } from '@mui/system';
import { Theme } from '@mui/material/styles';

import Avatar from '@mui/material/Avatar';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardHeader from '@mui/material/CardHeader';
import Skeleton from '@mui/material/Skeleton';
import Tooltip, { TooltipProps, tooltipClasses } from '@mui/material/Tooltip';
import Typography from '@mui/material/Typography';
import { styled } from '@mui/material/styles';

import { AuthenticatedGithubUser, GithubUser } from '../lib/common.types';
import { AnomyousGithubClient, GithubClient } from '../lib/GithubClient';

function githubAvatarUrl(githubLogin: string): string {
  return `https://github.com/${githubLogin}.png`;
}

interface GithubAvatarProps {
  sx?: SxProps<Theme>;
  githubAuth?: AuthenticatedGithubUser;
  githubLogin: string;
  tooltip?: boolean;
}

interface LoadingState {
  type: 'Loading'
}

interface LoadedState {
  type: 'Loaded',
  githubUser: GithubUser,
}

type State = LoadingState | LoadedState;

const LightTooltip = styled(({ className, ...props }: TooltipProps) => (
  <Tooltip {...props} classes={{ popper: className }} />
))(({ theme }) => ({
  [`& .${tooltipClasses.tooltip}`]: {
    backgroundColor: theme.palette.common.white,
    color: 'rgba(0, 0, 0, 0.87)',
    boxShadow: theme.shadows[1],
    fontSize: 11,
  },
}));

class GithubAvatarTooltip extends React.Component<GithubAvatarProps, State> {
  readonly state: Readonly<State> = {
    type: 'Loading'
  };

  componentDidMount() {
    const githubClient = (this.props.githubAuth ? new GithubClient({oauthToken: this.props.githubAuth?.oauthToken}) : new AnomyousGithubClient({}));
    githubClient.getUserByName(this.props.githubLogin).then((githubUser) => {
      this.setState({
        type: 'Loaded',
        githubUser
      });
    });
  }

  render() {
    return (
      <React.Fragment>
        <Card sx={{ width: 260, maxWidth: 345, border: '0', boxShadow: '0' }}>
          <CardHeader
            sx={{ pb: 0 }}
            avatar={
              <Avatar aria-label="recipe" alt={this.props.githubLogin} src={githubAvatarUrl(this.props.githubLogin)} />
            }
            title={ this.state.type === 'Loaded' ? (
                this.state.githubUser.name || this.state.githubUser.login
              ) : (
                <Skeleton
                  animation="wave"
                  height={10}
                  width="80%"
                  style={{ marginBottom: 6 }} />
              )
            }
            subheader={ this.state.type === 'Loaded' && this.state.githubUser.login ? (
              <Typography variant="caption" color="text.secondary" gutterBottom>
                {this.state.githubUser.login }
              </Typography>
            ) : (
              <Skeleton
                animation="wave"
                height={10}
                width="80%"
                style={{ marginBottom: 6 }} />
            )
          }
          />
          <CardContent>
          { this.state.type !== 'Loaded' ? (
            <>
              <Skeleton animation="wave" height={10} style={{ marginBottom: 6 }} />
              <Skeleton animation="wave" height={10} width="80%" />
            </>
          ) : (
            <>
              {/* <Typography variant="body2" color="text.secondary">
                <LocationOnIcon sx={{ fontSize: '12px' }} /> 
                { this.state.githubUser.location || <i>No Location.</i> }
              </Typography>
              <Typography variant="body2" color="text.secondary">
                { this.state.githubUser.bio || <i>No Bio.</i> }
              </Typography> */}
            </>
          ) }
          </CardContent>
        </Card>
      </React.Fragment>
    );
  }
}

export default class GithubAvatar extends React.Component<GithubAvatarProps, {}> {

  render() {
    return this.props.tooltip ? (
      <LightTooltip
        title={<GithubAvatarTooltip {...this.props}/>}
      >
        <Avatar sx={this.props.sx} aria-label="recipe" alt={this.props.githubLogin} src={githubAvatarUrl(this.props.githubLogin)} />
      </LightTooltip>
    ) : (
      <Avatar aria-label="recipe" alt={this.props.githubLogin} src={githubAvatarUrl(this.props.githubLogin)} />
    );
  }
}
