import React from 'react';

interface State {
}

export default class OAuthCodePage extends React.Component<{}, State> {

  messagePosted: boolean = false;

  render() {
    return (
      <p>You may close the tab now.</p>
    );
  }

  componentDidMount(): void {
    const queryParams = new URLSearchParams(window.location.search);
    const oauthCode = queryParams.get('code');
    if (oauthCode && !this.messagePosted) {
      this.messagePosted = true;
      window.opener?.postMessage({type: 'oauthCode', oauthCode: oauthCode});
    }
    window.close();
  }
}
