import React from 'react';

import Box from '@mui/material/Box';
import Fade from '@mui/material/Fade';

import ScrollTop from './ScrollTop';

interface RightButtomFabsProps {
}

interface State {
  show: boolean;
}

export default class RightButtomFabs extends React.Component<RightButtomFabsProps, State> {

  readonly state: Readonly<State> = {
    show: false
  };

  scrollEventHandler: () => void;
  
  constructor(props: RightButtomFabsProps) {
    super(props);
    this.scrollEventHandler = () => this.handleWindowScrollEvent();
  }

  handleWindowScrollEvent() {
    this.setState({
      show: window.pageYOffset >= 300
    });
  }

  shouldComponentUpdate(nextProps: Readonly<RightButtomFabsProps>, nextState: Readonly<State>, nextContext: any): boolean {
      return this.state.show !== nextState.show
  }

  componentDidMount(): void {
    window.addEventListener('scroll', this.scrollEventHandler);
    this.handleWindowScrollEvent();
  }

  componentWillUnmount(): void {
    window.removeEventListener('scroll', this.scrollEventHandler);
  }

  scrollToTop() {
    window.scroll(0, 0);
  }

  render() {
    return (
      <Fade in={this.state.show}>
        <Box
          role="presentation"
          sx={{
            position: 'fixed',
            bottom: 16,
            right: 16,
            '& > *': {
              ml: 2
            },
          }}
        >
          <ScrollTop/>
        </Box>
      </Fade>
    );
  }
}
