import React from 'react';

import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Divider from '@mui/material/Divider';
import Paper from '@mui/material/Paper';
import Tooltip from '@mui/material/Tooltip';

import { AuthenticatedGithubUser, GithubRepo } from '../lib/common.types';
import RightMenuOwners from './RightMenuOwners';

interface RightMenuProps extends GithubRepo {
  githubAuth?: AuthenticatedGithubUser;
}

export default class RightMenu extends React.Component<RightMenuProps, {}> {

  render() {
    return (
      <Paper>
        <Box sx={{ position: 'relative', p: 2 }}>
          {this.createDiscussionButton()}
          <Divider sx={{ mt: 2, mb: 2 }} />
          <h4 style={{ fontSize: '14px' }}>Owners</h4>
          <RightMenuOwners oauthToken={this.props.githubAuth?.oauthToken} user={this.props.user} repo={this.props.repo} />
        </Box>
      </Paper>
    );
  }

  createDiscussionButton() {
    if (this.props.githubAuth) {
      return (
        <Button sx={{ width: '100%' }} variant="outlined">
          Create Discussion
        </Button>
      );
    } else {
      return (
        <Tooltip title="Please login to create a discussion.">
          <div>
            <Button sx={{ width: '100%' }} variant="outlined" disabled>
              Create Discussion
            </Button>
          </div>
        </Tooltip>
      );
    }
  }
}