import React from 'react';
import { Link as ReactLink } from 'react-router-dom';

import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Grid from '@mui/material/Grid';
import Link from '@mui/material/Link';
import Typography from '@mui/material/Typography';

import { AuthenticatedGithubUser, GithubRepo } from '../lib/common.types';
import { DiscussionSummary } from '../lib/octokit-graphql.types';
import RightMenu from './RightMenu';
import LoadingTimelineCard from './LoadingTimelineCard';
import TimelineCard from './TimelineCard';
import { GithubClient } from '../lib/GithubClient';

interface TimelineProps extends GithubRepo {
  githubAuth?: AuthenticatedGithubUser;
}

interface State {
  loading: boolean;
  discussions: Array<DiscussionSummary>;
  nextCursor: string | undefined;
}

function discussionSummaryEquals(s1: DiscussionSummary, s2: DiscussionSummary): boolean {
  return s1 === s2 || s1.id === s2.id;
}

function discussionArrayEquals(a1: Array<DiscussionSummary>, a2: Array<DiscussionSummary>): boolean {
  if (a1 === a2) {
    return true;
  }
  if (a1 == null && a2 == null) {
    return true;
  }
  if (a1.length !== a2.length) {
    return false;
  }

  for (let i = 0; i < a1.length; ++i) {
    if (!discussionSummaryEquals(a1[i], a2[i])) {
      return false;
    }
  }
  return true;
}

export default class Timeline extends React.Component<TimelineProps, State> {

  scrollEventHandler: () => void;

  readonly state: Readonly<State> = {
    loading: true,
    discussions: [],
    nextCursor: undefined,
  };

  constructor(props: TimelineProps) {
    super(props);
    this.scrollEventHandler = () => this.handleWindowScrollEvent();
  }

  githubClient(): GithubClient | null {
    return this.props.githubAuth ? new GithubClient({
      oauthToken: this.props.githubAuth.oauthToken
    }) : null;
  }

  componentDidMount(): void {
    window.addEventListener('scroll', this.scrollEventHandler);
    this.loadFirstFewDiscussionCards();
  }

  componentDidUpdate(prevProps: Readonly<TimelineProps>, prevState: Readonly<{}>, snapshot?: any): void {
    if (this.props.repo !== prevProps.repo ||
        this.props.user !== prevProps.user ||
        (this.props.githubAuth && !prevProps.githubAuth)) {
      this.loadFirstFewDiscussionCards();
    }
  }

  async loadFirstFewDiscussionCards() {
    this.githubClient()?.getDiscussionSummuries({
      user: this.props.user,
      repo: this.props.repo,
      first: 10,
    }).then(repoDiscussions => {
      this.setState({
        loading: false,
        discussions: repoDiscussions.nodes,
        nextCursor: repoDiscussions.pageInfo.endCursor
      });
    });
  }

  componentWillUnmount(): void {
    window.removeEventListener('scroll', this.scrollEventHandler);
  }

  shouldComponentUpdate(nextProps: Readonly<TimelineProps>, nextState: Readonly<State>, nextContext: any): boolean {
    return this.props.user !== nextProps.user ||
      this.props.repo !== nextProps.repo ||
      this.props.githubAuth !== nextProps.githubAuth ||
      this.state.loading !== nextState.loading ||
      !discussionArrayEquals(this.state.discussions, nextState.discussions) || 
      this.state.nextCursor !== nextState.nextCursor;
  }

  handleWindowScrollEvent() {
    if ((window.innerHeight + window.pageYOffset) + 150 >= document.body.offsetHeight) {
      this.loadMoreDiscussions();
    }
  }

  loadMoreDiscussions() {
    if (this.state.loading) {
      return;
    }
    const loadingCursor = this.state.nextCursor;
    if (!loadingCursor) {
      return;
    }
    this.setState({
      loading: true,
    }, () => {
      this.githubClient()?.getDiscussionSummuries({
        user: this.props.user,
        repo: this.props.repo,
        first: 10,
        afterCursor: loadingCursor,
      }).then(repoDiscussionsOfNextPage => {
        this.setState((prevState) => {
          if (prevState.nextCursor !== loadingCursor) {
            return prevState;
          }
          const newdiscussions = prevState.discussions.concat(repoDiscussionsOfNextPage.nodes);
          return {
            loading: false,
            discussions: newdiscussions,
            nextCursor: repoDiscussionsOfNextPage.pageInfo.endCursor,
          };
        });
      });
    });
  }

  render() {
    return (
      <Box sx={{ flexGrow: 1, mt: 3 }}>
        <Grid container spacing={2}>
          <Grid item xl lg md={1} sm={0} xs={0} display={{ md: "block", sm: "none" }} />
          <Grid item xl={5} lg={7} md={8} sm={12} xs={12}>
            { this.renderCards() }
          </Grid>
          <Grid item xl={2} lg={3} md={2} sm={0} xs={0} display={{ md: "block", sm: "none", xs: "none" }}>
            <RightMenu githubAuth={this.props.githubAuth} user={this.props.user} repo={this.props.repo} />
            <footer className="main-footer">
              <Box sx={{
                display: 'flex',
                flexWrap: 'wrap',
                justifyContent: 'center',
                '& > *': {
                  fontSize: '10px',
                },
                '& > :not(style) + :not(style)': {
                  ml: 2,
                },
              }}>
                <Link component="div" href="https://github.com/octocus/octocus" underline="none">
                  <ReactLink to="https://github.com/octocus/octocus" style={{ textDecoration: 'inherit', color: 'inherit' }}>Github</ReactLink>
                </Link>
                <Link component="div" underline="none">
                  <ReactLink to="/_/octocus/octocus" style={{ textDecoration: 'inherit', color: 'inherit' }}>Meta</ReactLink>
                  </Link>
                <Link component="div" href="https://github.com/octocus/octocus/issues" underline="none">
                  <ReactLink to="https://github.com/octocus/octocus/issues/new" style={{ textDecoration: 'inherit', color: 'inherit' }}>Support</ReactLink>
                </Link>
              </Box>
              <p>Powered by <b>Octocus</b></p>
            </footer>
          </Grid>
          <Grid item xl lg md={1} sm={0} xs={0} display={{ md: "block", sm: "none" }}/>
        </Grid>
      </Box>
    );
  }

  renderCards() {
    if (!this.props.githubAuth) {
      return (
        <>
          <LoadingTimelineCard/>
          <LoadingTimelineCard/>
          <LoadingTimelineCard/>
        </>
      );
    }
    const githubAuth = this.props.githubAuth;
    if (this.state.discussions.length === 0) {
      // TODO
    }
    return (
      <>
        {
          this.state.discussions.map(summary => {
            return (
              <TimelineCard
                key={summary.id}
                githubAuth={githubAuth}
                user={this.props.user}
                repo={this.props.repo}
                summary={summary}
                />
            );
          })
        }
        {
          this.state.loading ? <LoadingTimelineCard/> : (
            this.state.nextCursor ? (
              <Button sx={{ width: '100%', mb: 2 }} variant="text" onClick={() => this.loadMoreDiscussions()}>More</Button>
            ) : (
              <Typography variant="caption" display="block" sx={{ textAlign: 'center' }} gutterBottom>No more discussions.</Typography>
            )
          )
        }
      </>
    );
  }
}
