import React from 'react';

import Box from '@mui/material/Box';
import List from '@mui/material/List';
import ListItem, { ListItemProps } from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import Skeleton from '@mui/material/Skeleton';
import Typography from '@mui/material/Typography';

import GithubAvatar from './GithubAvatar';
import durationSince from '../lib/DurationSince';
import { AuthenticatedGithubUser, GithubRepo } from '../lib/common.types';
import { GithubClient } from '../lib/GithubClient';
import { Actor, DiscussionComment } from '../lib/octokit-graphql.types';

const CommentListItem = (props: ListItemProps) => (
  <ListItem sx={{ display: 'block', borderLeft: '1px solid #e7e7e7', paddingLeft: '20px' }}>
    {props.children}
  </ListItem>
);

interface CommentsProps extends GithubRepo {
  githubAuth: AuthenticatedGithubUser;
  discussionNumber: number;
}

const CommentAvatar = (props: {
  githubAuth: AuthenticatedGithubUser;
  author?: Actor;
  publishedAt: string
}) => (
  <Box sx={{ display: 'flex', alignItems: 'center' }}>
    <ListItemAvatar style={{ minWidth: '40px' }}>
      { props.author ? (
        <GithubAvatar sx={{ width: '30px', height: '30px', mr: 1 }} tooltip githubAuth={props.githubAuth} githubLogin={props.author.login} />
      ) : (
        <Skeleton animation="wave" variant="circular" width={40} height={40} />
      )}
    </ListItemAvatar>
    <ListItemText
      secondary={
        <React.Fragment>
          <Typography
            // sx={{ display: 'block' }}
            component="span"
            variant="body2"
            color="text.primary"
          >
            { props.author ? props.author.login : 'Anonymous' }
          </Typography>
          <Typography
            sx={{ ml: 1, '&:before': { content: "'\\00B7'", mr: 1 } }}
            // sx={{ display: 'block' }}
            component="span"
            variant="body2"
            color="text.secondary"
          >
            { durationSince(props.publishedAt) }
          </Typography>
        </React.Fragment>
      }
    />
  </Box>
);

interface CommentProps {
  githubAuth: AuthenticatedGithubUser;
  comment: DiscussionComment;
  children?: JSX.Element;
}

const Comment = (props: CommentProps) => (
  <Box sx={{ mt: 4 }}>
    <CommentAvatar githubAuth={props.githubAuth} author={props.comment.author} publishedAt={props.comment.publishedAt || props.comment.createdAt} />
    <Typography
      sx={{ display: 'block', mt: 0, mb: 2, ml: '40px' }}
      component="span"
      variant="body2"
      color="text.primary"
    >
      <div className="markdown-body discussion-comment-body" dangerouslySetInnerHTML={{ __html: props.comment.bodyHTML }}/>
    </Typography>
    { props.children &&
      <List sx={{ width: '100%', bgcolor: 'background.paper' }}>
        <CommentListItem alignItems="flex-start">
          { props.children }
        </CommentListItem>
      </List>
    }
  </Box>
);

interface State {
  loading: boolean;
  comments: Array<DiscussionComment>;
  nextCursor: string | undefined;
}

export default class Comments extends React.Component<CommentsProps, State> {

  readonly state: Readonly<State> = {
    loading: true,
    comments: [],
    nextCursor: undefined,
  };

  componentDidMount(): void {
    new GithubClient({
      oauthToken: this.props.githubAuth.oauthToken
    }).getDiscussionComments({
      user: this.props.user,
      repo: this.props.repo,
      discussionNumber: this.props.discussionNumber,
      first: 10,
    }).then(connection => {
      this.setState({
        loading: false,
        comments: connection.nodes,
        nextCursor: connection.pageInfo.endCursor
      });
    })
  }

  loadMoreComments() {
    if (this.state.loading) {
      return;
    }
    const loadingCursor = this.state.nextCursor;
    if (!loadingCursor) {
      return;
    }
    this.setState({
      loading: true,
    }, () => {
      new GithubClient({
        oauthToken: this.props.githubAuth.oauthToken
      }).getDiscussionComments({
        user: this.props.user,
        repo: this.props.repo,
        discussionNumber: this.props.discussionNumber,
        first: 10,
        afterCursor: loadingCursor,
      }).then(connectionOnNextPage => {
        this.setState((prevState) => {
          if (prevState.nextCursor !== loadingCursor) {
            return prevState;
          }
          const newComments = prevState.comments.concat(connectionOnNextPage.nodes);
          return {
            loading: false,
            comments: newComments,
            nextCursor: connectionOnNextPage.pageInfo.endCursor,
          };
        });
      });
    });
  }

  render() {
    if (this.state.loading) {
      return <></>;
    }
    return (
      <>
      {
        this.state.comments.length === 0 && 
          <Typography textAlign="center" sx={{ fontSize: 12, color: '#888' }}>No Comments</Typography>
      }
      {
        this.state.comments.map(c => (
          <Comment key={c.id} githubAuth={this.props.githubAuth} comment={c}/>
        ))
      }
      </>
    )
  }
}
