import React from 'react';
import {
  HashRouter as Router,
  Route,
  Routes,
  useParams,
} from 'react-router-dom';

import './App.css';
import ErrorPage from './page/ErrorPage';
import MainPage from './page/MainPage';
import OAuthCodePage from './page/OAuthCodePage';

function MainPageWithParams() {
  const { user, repo } = useParams();
  if (!user || !repo) {
    return <ErrorPage errorMessage="Page not found"/>;
  }
  return <MainPage user={user} repo={repo}/>;;
}

function App() {
  const queryParams = new URLSearchParams(window.location.search);
  const oauthCode = queryParams.get('code');
  if (oauthCode) {
    return (<OAuthCodePage/>);
  }

  return (
    <Router>
      <Routes>
        <Route path="/_/:user/:repo/*" element={<MainPageWithParams/>}/>
      </Routes>
    </Router>
  );
}

export default App;
