import axios from 'axios';
import { Octokit } from '@octokit/rest';
import { RequestError } from '@octokit/request-error';

import {
  AuthenticatedGithubUser,
  GithubRepo,
  GithubOrgMember,
  GithubUser,
} from './common.types';
import {
  Discussion,
  DiscussionCommentConnection,
  RepositoryDiscussionsResponse,
  RepositoryDiscussionResponse,
  RepositoryDiscussionCommentsResponse,
  RepositoryDiscussionConnections,
} from './octokit-graphql.types';
import { GlobalConfig } from './GlobalConfig';

interface AnomyousGithubClientOptions {
  onRateLimitExceeded?: (() => void);
}

interface GithubClientOptions extends AnomyousGithubClientOptions {
  oauthToken: string;
}

interface PopupWindowSize {
  height: number;
  width: number;
}

export class AnomyousGithubClient {
  octokit: Octokit;
  config: AnomyousGithubClientOptions;

  constructor(config: AnomyousGithubClientOptions | undefined) {
    this.config = config || {};
    this.octokit = new Octokit();
  }

  async getUserByName(username: string): Promise<GithubUser> {
    const response = await this.octokit.users.getByUsername({username});
    const data = response.data;
    return {
      login: data.login,
      name: data.name,
      avatarUrl: data.avatar_url,
      location: data.location,
      bio: data.bio,
      since: new Date(data.created_at),
      type: data.type,
    };
  }

  async listOrgMembers(org: string): Promise<Array<GithubOrgMember>> {
    const response = await this.octokit.orgs.listMembers({
      org,
      per_page: 20,
    });
    const data = response.data;
    return data.map(user => ({
      login: user.login,
      name: user.name || null,
      avatarUrl: user.avatar_url,
    }));
  }

  popUpAuthWindow(windowSize: PopupWindowSize): void {
    const githubAuthUrl = new URL(GlobalConfig.github.oauthUrl);
    githubAuthUrl.searchParams.append('client_id', GlobalConfig.github.oauthAppClientId);
    githubAuthUrl.searchParams.append('scope', GlobalConfig.github.scopes);
    const redirectUri = new URL('/', window.location.href);
    githubAuthUrl.searchParams.append('redirect_uri', redirectUri.toString());
    window.open(githubAuthUrl, '_blank', `popup=yes,width=${windowSize.width},height=${windowSize.height}`);
  }

  async authenticateOAuthToken(oauthCode: string) {
    const params = new URLSearchParams();
    params.append('code', oauthCode);
    return await axios.post(GlobalConfig.githubOauthProxy.acessTokenUrl, params, {
      headers: {
        'Accept': 'application/json'
      }
    });
  }
}

interface DiscussionSummariesCriterion extends GithubRepo {
  first: number;
  afterCursor?: string;
}

interface DiscussionCriterion extends GithubRepo {
  discussionNumber: number;
}

interface DiscussionCommentsCriterion extends DiscussionCriterion {
  first: number;
  afterCursor?: string;
}

export class GithubClient extends AnomyousGithubClient {
  config: GithubClientOptions;

  constructor(config: GithubClientOptions) {
    super(config);
    this.config = config;
    this.octokit = new Octokit({
      auth: config.oauthToken
    });
    this.octokit.hook.error('request', async (error, options) => {
      if ((error instanceof RequestError) && error.response?.status === 403) {
        const remaining = error.response.headers['x-ratelimit-remaining'];
        if (remaining === '0') {
          this.config.onRateLimitExceeded?.();
          return;
        }
      }
      throw error;
    });
  }

  async getAuthenticatedUser(): Promise<AuthenticatedGithubUser> {
    const response = await this.octokit.users.getAuthenticated();
    const data = response.data;
    return {
      oauthToken: this.config.oauthToken,
      login: data.login,
      name: data.name,
      avatarUrl: data.avatar_url,
      location: data.location,
      bio: data.bio,
      since: new Date(data.created_at),
      type: data.type,
    };
  }

  async getDiscussionSummuries(criterion: DiscussionSummariesCriterion): Promise<RepositoryDiscussionConnections> {
    const discussionFilter = criterion.afterCursor
      ? `first: ${criterion.first}, after: "${criterion.afterCursor}"`
      : `first: ${criterion.first}`;
    const response = await this.octokit.graphql(`
      {
        repository(owner: "${criterion.user}", name: "${criterion.repo}") {
          discussions(${discussionFilter}) {
            # type: DiscussionConnection
            totalCount # Int!

            pageInfo {
              # type: PageInfo (from the public schema)
              startCursor
              endCursor
              hasNextPage
              hasPreviousPage
            }

            nodes {
              # type: Discussion
              id
              number
              title
              author {
                login
                avatarUrl
              }
              bodyText
              createdAt
              viewerHasUpvoted
            }
          }
        }
      }
    `) as RepositoryDiscussionsResponse;
    return response.repository.discussions;
  }

  async getDiscussion(criterion: DiscussionCriterion): Promise<Discussion> {
    const response = await this.octokit.graphql(`
      {
        repository(owner: "${criterion.user}", name: "${criterion.repo}") {
          discussion(number: ${criterion.discussionNumber}) {
            id
            number
            title
            author {
              login
              avatarUrl
            }
            bodyHTML
            createdAt
            viewerHasUpvoted
          }
        }
      }
    `) as RepositoryDiscussionResponse;
    return response.repository.discussion;
  }

  async getDiscussionComments(criterion: DiscussionCommentsCriterion): Promise<DiscussionCommentConnection> {
    const commentFilter = criterion.afterCursor
      ? `first: ${criterion.first}, after: "${criterion.afterCursor}"`
      : `first: ${criterion.first}`;
    const response = await this.octokit.graphql(`
      {
        repository(owner: "${criterion.user}", name: "${criterion.repo}") {
          discussion(number: ${criterion.discussionNumber}) {
            id
            comments(${commentFilter}) {
              # type: DiscussionCommentConnection
              totalCount # Int!
  
              pageInfo {
                # type: PageInfo (from the public schema)
                startCursor
                endCursor
                hasNextPage
                hasPreviousPage
              }
  
              nodes {
                # type: Comment
                author {
                  login
                  avatarUrl
                }
                body
                bodyHTML
                createdAt
                publishedAt
  
                isMinimized
                minimizedReason
                upvoteCount
              }
            }
          }
        }
      }
    `) as RepositoryDiscussionCommentsResponse;
    return response.repository.discussion.comments;
  }
}
