import React from 'react';
import { Link } from 'react-router-dom';

import Card from '@mui/material/Card';
import CardHeader from '@mui/material/CardHeader';
import CardContent from '@mui/material/CardContent';
import CardActions from '@mui/material/CardActions';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Skeleton from '@mui/material/Skeleton';
import FavoriteIcon from '@mui/icons-material/Favorite';
import ShareIcon from '@mui/icons-material/Share';
import MoreVertIcon from '@mui/icons-material/MoreVert';

import durationSince from '../lib/DurationSince';
import { AuthenticatedGithubUser, GithubRepo } from '../lib/common.types';
import { DiscussionSummary } from '../lib/octokit-graphql.types';
import GithubAvatar from './GithubAvatar';

interface TimelineCardProps extends GithubRepo {
  githubAuth: AuthenticatedGithubUser;
  summary: DiscussionSummary;
}

interface State {
}

export default class TimelineCard extends React.Component<TimelineCardProps, State> {

  readonly state: Readonly<State> = {
  };

  render() {
    return (
      <Card className='timeline-card'>
        <CardHeader
          avatar={(this.props.summary.author) ? (
            <GithubAvatar tooltip githubAuth={this.props.githubAuth} githubLogin={this.props.summary.author.login} />
          ) : (
            <Skeleton animation="wave" variant="circular" width={40} height={40} />
          )}
          action={
            <IconButton aria-label="settings">
              <MoreVertIcon />
            </IconButton>
          }
          title={(this.props.summary.author) ? (
            this.props.summary.author.login
          ) : (
            "Anonymous"
          )}
          subheader={
            durationSince(this.props.summary.createdAt)
          }
        />

        <CardContent sx={{ pt: 0 }}>
          <Link to={`/_/${this.props.user}/${this.props.repo}/discussion/${this.props.summary.number}`} style={{ textDecoration: 'none', color: 'inherit' }}>
            <Typography gutterBottom variant="h5" component="div" sx={{ fontSize: 20, cursor: 'pointer' }}>
              {this.props.summary.title}
            </Typography>
          </Link>
          <Typography variant="body2" color="text.secondary" sx={{ maxHeight: 200, overflow: 'hidden' }}>
            {this.props.summary.bodyText}
          </Typography>
        </CardContent>

        <CardActions disableSpacing>
          <IconButton aria-label="add to favorites">
            {
              this.props.summary.viewerHasUpvoted ? (
                <FavoriteIcon sx={{ color: 'red' }} />
              ) : (
                <FavoriteIcon />
              )
            }
          </IconButton>
          <IconButton aria-label="share">
            <ShareIcon />
          </IconButton>
        </CardActions>
      </Card>
    );
  }
}
