import React from 'react';
import { Link } from 'react-router-dom';

import AppBar from '@mui/material/AppBar';
import Avatar from '@mui/material/Avatar';
import Box from '@mui/material/Box';
import IconButton from '@mui/material/IconButton';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import Toolbar from '@mui/material/Toolbar';
import Tooltip from '@mui/material/Tooltip';
import Typography from '@mui/material/Typography';

import { AuthenticatedGithubUser, GithubRepo } from '../lib/common.types';

interface MainHeaderProps extends GithubRepo {
  githubAuth?: AuthenticatedGithubUser;
  onLogout?: (() => void);
}

interface State {
  showLoginModal: boolean;
  anchorElUserMenu: null | HTMLElement;
}

export default class MainHeader extends React.Component<MainHeaderProps, State> {

  readonly state: Readonly<State> = {
    showLoginModal: false,
    anchorElUserMenu: null
  };

  readonly modalStyle = {
    position: 'absolute' as 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
  };

  render() {
    let avatarOrLoginButton;
    if (!this.props.githubAuth) {
      avatarOrLoginButton = <></>;
    } else {
      avatarOrLoginButton = (
        <>
          <Tooltip title="User Settings">
            <IconButton onClick={e => this.handleOpenUserMenu(e)} sx={{ p: 0 }}>
              <Avatar alt={this.props.githubAuth.name || undefined} src={this.props.githubAuth.avatarUrl} />
            </IconButton>
          </Tooltip>
          <Menu
            sx={{ mt: '45px' }}
            id="menu-appbar"
            anchorEl={this.state.anchorElUserMenu}
            anchorOrigin={{
              vertical: 'top',
              horizontal: 'right',
            }}
            keepMounted
            transformOrigin={{
              vertical: 'top',
              horizontal: 'right',
            }}
            open={Boolean(this.state.anchorElUserMenu)}
            onClose={() => this.handleCloseUserMenu()}
          >
            <MenuItem key="Logout" onClick={() => this.onLogoutClick()}>
              <Typography textAlign="center">Logout</Typography>
            </MenuItem>
          </Menu>
        </>
      );
    }
    return (
      <>
        <Box sx={{ flexGrow: 1 }}>
          <AppBar position="fixed" sx={{ backgroundColor: '#fff', color: '#000' }}>
            <Toolbar>
              <Link to={`/_/${this.props.user}/${this.props.repo}`} style={{ textDecoration: 'none', color: 'inherit' }}>
                <Typography
                  variant="h6"
                  noWrap
                  component="span"
                  sx={{
                    mr: 2,
                    display: 'flex',
                    fontFamily: 'monospace',
                    fontWeight: 700,
                    letterSpacing: '.3rem',
                    color: 'inherit',
                    textDecoration: 'none',
                    cursor: 'pointer',
                  }}
                >
                  Octocus
                </Typography>
              </Link>
              <Box sx={{ flexGrow: 1, display: 'flex' }}></Box>
              <Box sx={{ flexGrow: 0 }}>
                { avatarOrLoginButton }
              </Box>
            </Toolbar>
          </AppBar>
        </Box>
      </>
    );
  }

  handleLoginModalClose() {
    this.setState({
      showLoginModal: false
    });
  }

  handleOpenUserMenu(event: React.MouseEvent<HTMLElement>) {
    this.setState({
      anchorElUserMenu: event.currentTarget
    });
  }

  handleCloseUserMenu(callback?: () => void) {
    this.setState({
      anchorElUserMenu: null
    }, callback);
  }

  onLogoutClick() {
    this.handleCloseUserMenu(() => {
      this.props.onLogout?.();
    });
  }
}
