import React from 'react';
import {
  Route,
  Routes,
} from 'react-router-dom';

import Box from '@mui/material/Box';
import { ThemeProvider, createTheme } from '@mui/material/styles';

import DiscussionThread from '../component/DiscussionThread';
import GithubLoginDialog from '../component/GithubLoginDialog';
import MainHeader from '../component/MainHeader';
import RightButtomFabs from '../component/RightButtomFabs';
import Timeline from '../component/Timeline';
import { AuthenticatedGithubUser, GithubRepo } from '../lib/common.types';
import { GithubClient } from '../lib/GithubClient';

interface MainPageProps extends GithubRepo {
}

interface LoadingState {
  type: 'Loading';
  githubAuth: undefined;
}

interface AnonymousState {
  type: 'Anonymous';
  githubAuth: undefined;
}

interface AuthorizedState {
  type: 'Authorized';
  githubAuth: AuthenticatedGithubUser;
}

type State = LoadingState | AnonymousState | AuthorizedState;

const THEME = createTheme({
  palette: {
    primary: {
      light: '#757ce8',
      main: '#3f50b5',
      dark: '#002884',
      contrastText: '#fff',
    },
    secondary: {
      light: '#ff7961',
      main: '#f44336',
      dark: '#ba000d',
      contrastText: '#000',
    },
  },
});

export default class MainPage extends React.Component<MainPageProps, State> {

  readonly OAUTH_KEY_NAME = 'githubOauthToken';

  readonly state: Readonly<State> = {
    type: 'Loading',
    githubAuth: undefined,
  };

  render() {
    if (this.state.type === 'Loading') {
      return <></>;
    }
    return (
      <>
        <ThemeProvider theme={THEME}>
          <>
            <MainHeader user={this.props.user} repo={this.props.repo} githubAuth={this.state.githubAuth} onLogout={() => this.handleLogout()} />
            { this.renderSubPage() }
            { this.maybeRenderLoginDialog() }
            <RightButtomFabs/>
          </>
        </ThemeProvider>
      </>
    );
  }

  renderSubPage() {
    return (
      <Box sx={{ mt: { xs: '60px', sm: '60px', md: '84px' } }}>
        <Routes>
          <Route path="" element={
            <Timeline
              githubAuth={this.state.githubAuth}
              user={this.props.user}
              repo={this.props.repo}
              />
          }/>
          <Route path="discussion/:discussionNumber" element={
            <DiscussionThread 
              githubAuth={this.state.githubAuth}
              user={this.props.user}
              repo={this.props.repo}
              />
          }/>
        </Routes>
      </Box>
    );
  }

  maybeRenderLoginDialog() {
    if (this.state.githubAuth) {
      return <></>
    }
    return (
      <GithubLoginDialog
        open={true}
        onOAuthTokenGranted={(oauthToken) => this.handleOAuthTokenGranted(oauthToken)}/>
    )
  }

  componentDidMount(): void {
    this.componentDidMountOrUpdate();
  }

  componentDidUpdate(prevProps: Readonly<MainPageProps>, prevState: Readonly<State>, snapshot?: any): void {
    this.componentDidMountOrUpdate();
  }

  componentDidMountOrUpdate() {
    const tokenInLocalStorage = window.localStorage.getItem(this.OAUTH_KEY_NAME);
    if (tokenInLocalStorage) {
      this.verifyGithubOAuthTokenAndSetState(tokenInLocalStorage);
    } else {
      this.setMainPageState();
    }
  }

  shouldComponentUpdate(nextProps: Readonly<MainPageProps>, nextState: Readonly<State>, nextContext: any): boolean {
    return this.props.user !== nextProps.user ||
      this.props.repo !== nextProps.repo ||
      this.state.type !== nextState.type ||
      this.state.githubAuth?.oauthToken !== nextState.githubAuth?.oauthToken;
  }

  setMainPageState(githubAuth?: AuthenticatedGithubUser) {
    this.setState({
      type: githubAuth ? 'Authorized' : 'Anonymous',
      githubAuth
    });
  }

  handleOAuthTokenGranted(oauthToken: string) {
    window.localStorage.setItem(this.OAUTH_KEY_NAME, oauthToken);
    this.verifyGithubOAuthTokenAndSetState(oauthToken);
  }

  handleLogout() {
    window.localStorage.removeItem(this.OAUTH_KEY_NAME);
    this.setMainPageState();
  }

  verifyGithubOAuthTokenAndSetState(oauthToken: string) {
    new GithubClient({
      oauthToken,
      onRateLimitExceeded: () => this.handleRateLimitExceeded(),
    }).getAuthenticatedUser().then((user) => {
      this.setMainPageState({
        ...user
      });
    }).catch(() => {
      this.setMainPageState();
    });
  }

  handleRateLimitExceeded() {
    // TODO
  }
}
