function missingRequireEnvironmentVariableError(name: string): Error {
  console.log(`Here are the available environment variables: ${JSON.stringify(process.env)}.`);
  return Error(`Missing required environment variable: ${name}`);
}

if (!process.env.REACT_APP_GITHUB_CLIENT_ID) {
  throw missingRequireEnvironmentVariableError('REACT_APP_GITHUB_CLIENT_ID');
}
if (!process.env.REACT_APP_GITHUB_OAUTH_PROXY_URL) {
  throw missingRequireEnvironmentVariableError('REACT_APP_GITHUB_OAUTH_PROXY_URL');
}

export let GlobalConfig = {
  github: {
    oauthUrl: process.env.REACT_APP_GITHUB_OAUTH_URL || 'https://github.com/login/oauth/authorize',
    oauthAppClientId: process.env.REACT_APP_GITHUB_CLIENT_ID,
    scopes: 'public_repo',
  },
  githubOauthProxy: {
    acessTokenUrl: process.env.REACT_APP_GITHUB_OAUTH_PROXY_URL,
  },
};
