import React from 'react';

import Backdrop from '@mui/material/Backdrop';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import CheckIcon from '@mui/icons-material/Check';
import CircularProgress from '@mui/material/CircularProgress';
import Dialog, {DialogProps} from '@mui/material/Dialog';

import GithubLogo from './GithubLogo';
import { AnomyousGithubClient } from '../lib/GithubClient';

interface Props extends DialogProps {
  onOAuthTokenGranted?: (oauthToken: string) => void;
  onOAuthTokenFailure?: (error: any) => void;
}

interface AnonymousState {
  type: 'Anonymous'
}

interface RequestingOAuthCodeState {
  type: 'RequestingOAuthCode'
}

interface OAuthTokenGrantedState {
  type: 'OAuthTokenGranted',
  bearerToken: string
}

type State = AnonymousState | RequestingOAuthCodeState | OAuthTokenGrantedState;

export default class GithubLoginDialog extends React.Component<Props, State> {
  readonly state: State = {
    type: 'Anonymous'
  };

  render() {
    const { onOAuthTokenGranted, onOAuthTokenFailure, ...dialogProps } = this.props;
    return (
      <Dialog {...dialogProps}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
          classes: {
            root: '.'
          }
        }}
        sx={{
          backdropFilter: "blur(5px)",
        }}
      >
        <div className='login-modal-container'>
          <h2>Log In</h2>
          <p>By continuing, you will be redirected to Github.com.</p>
          { this.showButton() }
        </div>
      </Dialog>
    );
  }

  showButton(): JSX.Element {
    switch (this.state.type) {
      case 'Anonymous':
        return (
          <Box sx={{ position: 'relative' }}>
            <Button variant="outlined" startIcon={<GithubLogo/>} onClick={() => this.loginWithGithub()}>
              Continue with Github
            </Button>
          </Box>
        );
      case 'RequestingOAuthCode':
        return (
          <Box sx={{ position: 'relative' }}>
            <Button variant="outlined" startIcon={<GithubLogo/>} disabled>
              Continue with Github
            </Button>
            <CircularProgress
              size={24}
              sx={{
                position: 'absolute',
                top: '50%',
                left: '50%',
                marginTop: '-12px',
                marginLeft: '-12px',
              }}
            />
          </Box>
        );
      case 'OAuthTokenGranted':
        return (
          <Button variant="outlined" disabled>
            <CheckIcon />
          </Button>
        );
    }
  }

  readonly oauthCodeCallback = async (e: MessageEvent<any>) => {
    if ('oauthCode' === e.data?.type) {
      let res;
      try {
        res = await new AnomyousGithubClient({}).authenticateOAuthToken(e.data.oauthCode);
      } catch (e) {
        this.props.onOAuthTokenFailure?.(e);
        return;
      }
      const bearerToken = res.data['access_token'] as string | undefined | null;
      if (!bearerToken) {
        this.props.onOAuthTokenFailure?.(new Error('cannot find access_token in the response payload.'));
        return;
      }
      this.setState({
        type: 'OAuthTokenGranted',
        bearerToken
      });
      this.props.onOAuthTokenGranted?.('bearer ' + bearerToken);
    }
  };

  loginWithGithub() {
    window.removeEventListener('message', this.oauthCodeCallback);
    window.addEventListener('message', this.oauthCodeCallback);
    this.setState({
      type: 'RequestingOAuthCode'
    });

    new AnomyousGithubClient({}).popUpAuthWindow({
      width: 600,
      height: 780,
    });
  }
}
