import React from 'react';

import Avatar from '@mui/material/Avatar';
import AvatarGroup from '@mui/material/AvatarGroup';
import Grid from '@mui/material/Grid';
import Skeleton from '@mui/material/Skeleton';

import { GithubRepo, GithubOrgMember } from '../lib/common.types';
import { AnomyousGithubClient, GithubClient } from '../lib/GithubClient';

interface RightMenuOwnersProps extends GithubRepo {
  oauthToken?: string;
  user: string;
}

interface State {
  loading: boolean;
  members: Array<GithubOrgMember>;
}

export default class RightMenuOwners extends React.Component<RightMenuOwnersProps, State> {
  readonly state: Readonly<State> = {
    loading: true,
    members: [],
  };

  async componentDidMountOrUpdate() {
    const githubClient = (this.props.oauthToken
      ? new GithubClient({oauthToken: this.props.oauthToken})
      : new AnomyousGithubClient({}));
    const user = await githubClient.getUserByName(this.props.user);
    if (user.type === 'Organization') {
      const members = await githubClient.listOrgMembers(this.props.user);
      this.setState({
        loading: false,
        members,
      });
    } else {
      this.setState({
        loading: false,
        members: [{
          login: user.login,
          name: user.name,
          avatarUrl: user.avatarUrl,
        }]
      });
    }
  }

  componentDidMount(): void {
    this.componentDidMountOrUpdate();
  }

  componentDidUpdate(prevProps: Readonly<RightMenuOwnersProps>, prevState: Readonly<State>, snapshot?: any): void {
    this.componentDidMountOrUpdate();
  }

  shouldComponentUpdate(nextProps: Readonly<RightMenuOwnersProps>, nextState: Readonly<State>, nextContext: any): boolean {
    return this.state.loading !== nextState.loading ||
      this.props.oauthToken !== nextProps.oauthToken ||
      this.props.user !== nextProps.user ||
      this.props.repo !== nextProps.repo;
  }

  render(): React.ReactNode {
    if (this.state.loading) {
      return 
    }
    return (
      <Grid display="flex" sx={{ mb: 2 }}>
        {
          (this.state.loading) ? (
            <Skeleton animation="wave" variant="circular" width={40} height={40} />
          ) : (
            <AvatarGroup max={4}>
              {
                this.state.members.map(member => (
                  <Avatar key={member.login} alt={member.name || ''} src={member.avatarUrl} />
                ))
              }
            </AvatarGroup>
          )
        }
      </Grid>
    );
  }
}