function toDate(date: Date | string | number): Date {
  return new Date(date);
}

const ONE_SECOND_MILLIS = 1000;
const ONE_MINUTE_MILLIS = 60 * ONE_SECOND_MILLIS;
const ONE_HOUR_MILLIS = 60 * ONE_MINUTE_MILLIS;
const ONE_DAY_MILLIS = 24 * ONE_HOUR_MILLIS;
const ONE_WEEK_MILLIS = 7 * ONE_DAY_MILLIS;
const ONE_MONTH_MILLIS = 30 * ONE_DAY_MILLIS;
const ONE_YEAR_IN_SECONDS = 365 * ONE_DAY_MILLIS;

interface Formatter {
  unit: string,
  toMillis: number,
}

const FORMATTERS: Array<Formatter> = [
  {
    unit: 'year',
    toMillis: ONE_YEAR_IN_SECONDS,
  },
  {
    unit: 'month',
    toMillis: ONE_MONTH_MILLIS,
  },
  {
    unit: 'week',
    toMillis: ONE_WEEK_MILLIS,
  },
  {
    unit: 'day',
    toMillis: ONE_DAY_MILLIS,
  },
  {
    unit: 'hour',
    toMillis: ONE_HOUR_MILLIS,
  },
  {
    unit: 'minute',
    toMillis: ONE_MINUTE_MILLIS,
  },
  {
    unit: 'second',
    toMillis: ONE_SECOND_MILLIS,
  },
];

export default function durationSince(date: Date | string | number): string {
  const sinceWhen = toDate(date);
  const now = new Date();
  const durationBetweenMills = Math.abs(now.getTime() - sinceWhen.getTime());
  
  const durationWithUnit = (() => {
    for (let formatter of FORMATTERS) {
      const valInUnit = Math.floor(durationBetweenMills / formatter.toMillis);
      if (valInUnit > 1) {
        return Math.floor(valInUnit) + " " + formatter.unit + "s";
      } else if (valInUnit === 1) {
        return valInUnit + " " + formatter.unit;
      }
    }
  })();
  return durationWithUnit ? durationWithUnit + " ago" : "just now";
}
