import React from 'react';

import Card from '@mui/material/Card';
import CardHeader from '@mui/material/CardHeader';
import CardContent from '@mui/material/CardContent';
import CardActions from '@mui/material/CardActions';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Skeleton from '@mui/material/Skeleton';
import FavoriteIcon from '@mui/icons-material/Favorite';
import ShareIcon from '@mui/icons-material/Share';
import MoreVertIcon from '@mui/icons-material/MoreVert';

import durationSince from '../lib/DurationSince';
import { AuthenticatedGithubUser, GithubRepo } from '../lib/common.types';
import { Discussion } from '../lib/octokit-graphql.types';
import Comments from './Comments';
import GithubAvatar from './GithubAvatar';

interface DiscussionCardProps extends GithubRepo {
  githubAuth: AuthenticatedGithubUser;
  discussion: Discussion;
}

interface State {
}

export default class DiscussionCard extends React.Component<DiscussionCardProps, State> {

  readonly state: Readonly<State> = {
  };

  render() {
    return (
      <Card className='timeline-card' sx={{ p: 1 }}>
        <CardHeader
          avatar={(this.props.discussion.author) ? (
            <GithubAvatar tooltip githubAuth={this.props.githubAuth} githubLogin={this.props.discussion.author.login} />
          ) : (
            <Skeleton animation="wave" variant="circular" width={40} height={40} />
          )}
          action={
            <IconButton aria-label="settings">
              <MoreVertIcon />
            </IconButton>
          }
          title={(this.props.discussion.author) ? (
            this.props.discussion.author.login
          ) : (
            "Anonymous"
          )}
          subheader={
            durationSince(this.props.discussion.createdAt)
          }
        />

        <CardContent sx={{ pt: 0 }}>
          <Typography gutterBottom variant="h2" component="div" sx={{ fontSize: '25px', fontWeight: 'bold', mb: 3 }}>
            {this.props.discussion.title}
          </Typography>
          <Typography variant="body2" color="text.primary">
            <div className="markdown-body discussion-body" dangerouslySetInnerHTML={{ __html: this.props.discussion.bodyHTML }} style={{ fontSize: '16px' }}/>
          </Typography>
        </CardContent>

        <CardActions disableSpacing>
          <IconButton aria-label="add to favorites">
            {
              this.props.discussion.viewerHasUpvoted ? (
                <FavoriteIcon sx={{ color: 'red' }} />
              ) : (
                <FavoriteIcon />
              )
            }
          </IconButton>
          <IconButton aria-label="share">
            <ShareIcon />
          </IconButton>
        </CardActions>

        <CardContent>
          <Divider variant="middle" sx={{ mt: 0, mb: 2 }} />

          <Typography variant="h6" sx={{ mb: 2 }}>
            Comments
          </Typography>
          <Comments
            user={this.props.user}
            repo={this.props.repo}
            githubAuth={this.props.githubAuth}
            discussionNumber={this.props.discussion.number} />
        </CardContent>
      </Card>
    );
  }
}
