import React from 'react';
import { useParams } from 'react-router-dom';

import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';

import { AuthenticatedGithubUser, GithubRepo } from '../lib/common.types';
import { Discussion } from '../lib/octokit-graphql.types';
import DiscussionCard from './DiscussionCard';
import LoadingTimelineCard from './LoadingTimelineCard';
import { GithubClient } from '../lib/GithubClient';

interface DiscussionThreadProps extends GithubRepo {
  githubAuth?: AuthenticatedGithubUser;
}

interface DiscussionThreadComponentProps extends DiscussionThreadProps {
  githubAuth?: AuthenticatedGithubUser;
  discussionNumber: number;
}

interface State {
  discussion?: Discussion;
}

class DiscussionThreadComponent extends React.Component<DiscussionThreadComponentProps, State> {

  readonly state: Readonly<State> = {
  };

  render() {
    return (
      <Box sx={{ flexGrow: 1, mt: 3 }}>
        <Grid container spacing={2}>
          <Grid item xl lg md={1} sm={0} xs={0} display={{ md: "block", sm: "none" }} />
          <Grid item  xl={5} lg={7} md={8} sm={12} xs={12}>
            { this.renderContent() }
          </Grid>
          <Grid item xl lg md={1} sm={0} xs={0} display={{ md: "block", sm: "none" }} />
        </Grid>
      </Box>
    );
  }

  renderContent() {
    if (!this.props.githubAuth || !this.state.discussion) {
      return (
        <LoadingTimelineCard/>
      );
    }
    return <DiscussionCard user={this.props.user} repo={this.props.repo} githubAuth={this.props.githubAuth} discussion={this.state.discussion} />;
  }

  componentDidMount(): void {
    this.componentDidMountOrUpdate();
  }

  componentDidUpdate(prevProps: Readonly<DiscussionThreadComponentProps>, prevState: Readonly<State>, snapshot?: any): void {
    this.componentDidMountOrUpdate();
  }

  shouldComponentUpdate(nextProps: Readonly<DiscussionThreadComponentProps>, nextState: Readonly<State>, nextContext: any): boolean {
    return (this.props.githubAuth !== nextProps.githubAuth) ||
      (this.props.repo !== nextProps.repo) ||
      (this.props.user !== nextProps.user) ||
      (!this.state.discussion);
  }

  async componentDidMountOrUpdate() {
    if (!this.props.githubAuth) {
      return;
    }
    const discussion = await new GithubClient({oauthToken: this.props.githubAuth.oauthToken}).getDiscussion({
      user: this.props.user,
      repo: this.props.repo,
      discussionNumber: this.props.discussionNumber,
    });
    this.setState({
      discussion
    });
  }
}

export default function DiscussionThread(props: DiscussionThreadProps) {
  const { discussionNumber } = useParams();
  if (!discussionNumber) {
    return <></>;
  }
  return <DiscussionThreadComponent user={props.user} repo={props.repo} githubAuth={props.githubAuth} discussionNumber={Number(discussionNumber)}/>;
};
